.active-team {
    margin-top: 6.8%;
    width: 38.33%;
    height: 56.11%;
    font-size: 2vh;
}

.active-team-1 {
    background-color: rgba(0, 87, 255, 0.64);
    box-shadow: 0 0 6px 0 #ffffff, 0 0 10px 0 #ffffff;
    border: solid 2px #ffffff;
}

.active-team-2 {
    background-image: linear-gradient(to right, #ac0000, #ba0606 100%);
    box-shadow: 0 0 6px 0 #ffffff, 0 0 10px 0 #ffffff;
    border: solid 2px #ffffff;
}

.active-team-1-points {
    text-shadow: 0 0 9px #45b6fc, 0 0 4px #45b6fc;
    -webkit-text-stroke: 1px #ffffff;
}

.active-team-2-points {
    text-shadow: 0 0 9px #fa8a64, 0 0 4px #fa8a64;
    -webkit-text-stroke: 1px #ffffff;
}

